import React from "react";
import {Field, WrappedFieldMetaProps, WrappedFieldProps} from "redux-form";

import styles from "./FormsControls.module.scss";

import {FieldValidatorType} from "../../../units/validators/validators";


type FormsControlPropsType = {
    meta: WrappedFieldMetaProps
}

const FormControl: React.FC<FormsControlPropsType> = ({meta: {touched, error}, children}) => {
    const hasError = touched && error;
    return (
        <div className={styles.formControl + " " + (hasError ? styles.error : "")}>
            <div>
                {children}
            </div>
            {hasError && <span>{error}</span>}
        </div>
    )
};

export const TextArea: React.FC<WrappedFieldProps> = (props) => {
    const {input, meta, ...restProps} = props;
    return <FormControl {...props}><textarea{...input} {...restProps}/></FormControl>
};

export const Input: React.FC<WrappedFieldProps> = (props) => {
    const {input, meta, ...restProps} = props;
    return <FormControl {...props}><input{...input} {...restProps}/></FormControl>
};

export function createField<FormsKeysType extends string>(placeholder: string | undefined,
                                      name: FormsKeysType,
                                      validators: Array<FieldValidatorType>,
                                      component: React.FC<WrappedFieldProps>,
                                      props = {}, text = " ") {
    return (
        <div>
            <Field placeholder={placeholder} name={name}
                   validate={validators} component={component}
                   {...props}/> {text}
        </div>
    )
}

export type GetStringKeys<T> = Extract<keyof T, string>
